import { useState, useEffect } from 'react'
import { motion, AnimatePresence } from 'framer-motion'
import { useTranslation } from 'react-i18next'
import {
    MapPin,
    AlertTriangle,
    Activity,
    Clock,
    Bus,
    Navigation,
    CheckCircle,
    XCircle,
    ChevronRight,
    Zap
} from 'lucide-react'

interface Alert {
    id: string
    type: 'critical' | 'warning' | 'info'
    message: string
    vehicleId?: string
    time: Date
}

interface VehiclePosition {
    id: string
    plateNumber: string
    routeName: string
    lat: number
    lng: number
    speed: number
    status: 'normal' | 'delayed' | 'stopped'
}

interface SignageInfo {
    routeCode: string
    routeName: string
    destination: string
    nextArrival: number
    vehicleNumber: string
}

// 模拟数据生成器
const generateMockAlerts = (): Alert[] => [
    { id: '1', type: 'critical', message: '车辆 粤B12345 超员警告', vehicleId: 'v1', time: new Date() },
    { id: '2', type: 'warning', message: '线路 101 延误 15 分钟', time: new Date(Date.now() - 300000) },
    { id: '3', type: 'info', message: '车辆 粤B67890 已完成保养', vehicleId: 'v2', time: new Date(Date.now() - 600000) },
]

const generateMockVehicles = (): VehiclePosition[] => [
    { id: 'v1', plateNumber: '粤B12345', routeName: '101路', lat: 22.5431, lng: 114.0579, speed: 35, status: 'normal' },
    { id: 'v2', plateNumber: '粤B67890', routeName: '202路', lat: 22.5521, lng: 114.0679, speed: 0, status: 'stopped' },
    { id: 'v3', plateNumber: '粤B11111', routeName: '303路', lat: 22.5321, lng: 114.0479, speed: 28, status: 'delayed' },
]

const generateMockSignage = (): SignageInfo[] => [
    { routeCode: '101', routeName: '101路', destination: '火车站', nextArrival: 3, vehicleNumber: '粤B12345' },
    { routeCode: '202', routeName: '202路', destination: '科技园', nextArrival: 8, vehicleNumber: '粤B67890' },
    { routeCode: '303', routeName: '303路', destination: '机场东', nextArrival: 12, vehicleNumber: '粤B11111' },
]

export function MonitoringPanel() {
    const { t } = useTranslation()
    const [alerts, setAlerts] = useState<Alert[]>(generateMockAlerts())
    const [vehicles] = useState<VehiclePosition[]>(generateMockVehicles())
    const [signage] = useState<SignageInfo[]>(generateMockSignage())
    const [activeSection, setActiveSection] = useState<'map' | 'alerts' | 'kpi' | 'signage'>('map')

    // 模拟实时数据更新
    useEffect(() => {
        const interval = setInterval(() => {
            // 随机添加新告警
            if (Math.random() > 0.7) {
                const newAlert: Alert = {
                    id: Date.now().toString(),
                    type: Math.random() > 0.5 ? 'warning' : 'info',
                    message: `车辆 粤B${Math.floor(Math.random() * 90000) + 10000} 状态更新`,
                    time: new Date()
                }
                setAlerts(prev => [newAlert, ...prev.slice(0, 9)])
            }
        }, 10000)
        return () => clearInterval(interval)
    }, [])

    const kpiMetrics = [
        { label: t('transit.onlineVehiclesCount'), value: '24', icon: Bus, color: 'success' },
        { label: t('transit.avgDepartureInterval'), value: '8', unit: t('transit.minutes'), icon: Clock, color: 'primary' },
        { label: t('transit.abnormalRate'), value: '2.3%', icon: Activity, color: 'warning' },
    ]

    const getAlertColor = (type: Alert['type']) => {
        switch (type) {
            case 'critical': return 'text-red-500 bg-red-500/10 border-red-500/20'
            case 'warning': return 'text-amber-500 bg-amber-500/10 border-amber-500/20'
            case 'info': return 'text-blue-500 bg-blue-500/10 border-blue-500/20'
        }
    }

    const getStatusColor = (status: VehiclePosition['status']) => {
        switch (status) {
            case 'normal': return 'bg-green-500'
            case 'delayed': return 'bg-amber-500'
            case 'stopped': return 'bg-slate-400'
        }
    }

    return (
        <div className="w-80 bg-white dark:bg-dark-800 border-l border-slate-200 dark:border-dark-600 flex flex-col h-full overflow-hidden">
            {/* 标题 */}
            <div className="p-4 border-b border-slate-200 dark:border-dark-600">
                <h3 className="font-semibold text-slate-900 dark:text-slate-100 flex items-center gap-2">
                    <Activity className="w-5 h-5 text-primary-500" />
                    {t('transit.monitoring')}
                </h3>
            </div>

            {/* 分段选择器 */}
            <div className="flex border-b border-slate-200 dark:border-dark-600">
                {[
                    { key: 'map', icon: MapPin },
                    { key: 'alerts', icon: AlertTriangle },
                    { key: 'kpi', icon: Zap },
                    { key: 'signage', icon: Clock },
                ].map(({ key, icon: Icon }) => (
                    <button
                        key={key}
                        onClick={() => setActiveSection(key as any)}
                        className={`flex-1 p-3 flex justify-center transition-colors ${activeSection === key
                                ? 'bg-primary-500/10 text-primary-600 dark:text-primary-400 border-b-2 border-primary-500'
                                : 'text-slate-500 hover:text-slate-700 dark:hover:text-slate-300'
                            }`}
                    >
                        <Icon className="w-5 h-5" />
                    </button>
                ))}
            </div>

            {/* 内容区 */}
            <div className="flex-1 overflow-y-auto">
                <AnimatePresence mode="wait">
                    {/* 地图/车辆位置 */}
                    {activeSection === 'map' && (
                        <motion.div
                            key="map"
                            initial={{ opacity: 0 }}
                            animate={{ opacity: 1 }}
                            exit={{ opacity: 0 }}
                            className="p-4 space-y-3"
                        >
                            <div className="aspect-video bg-gradient-to-br from-slate-100 to-slate-200 dark:from-dark-700 dark:to-dark-600 rounded-lg flex items-center justify-center relative overflow-hidden">
                                <div className="absolute inset-0 opacity-20">
                                    <div className="w-full h-full" style={{ background: 'repeating-linear-gradient(0deg, transparent, transparent 20px, rgba(0,0,0,0.05) 20px, rgba(0,0,0,0.05) 21px), repeating-linear-gradient(90deg, transparent, transparent 20px, rgba(0,0,0,0.05) 20px, rgba(0,0,0,0.05) 21px)' }} />
                                </div>
                                {vehicles.map((v, i) => (
                                    <motion.div
                                        key={v.id}
                                        className={`absolute w-3 h-3 rounded-full ${getStatusColor(v.status)}`}
                                        style={{ left: `${30 + i * 20}%`, top: `${40 + i * 10}%` }}
                                        animate={{ scale: [1, 1.2, 1] }}
                                        transition={{ repeat: Infinity, duration: 2, delay: i * 0.3 }}
                                    />
                                ))}
                                <MapPin className="w-8 h-8 text-slate-400" />
                            </div>
                            <p className="text-xs text-slate-500 text-center">{t('transit.realtimePosition')}</p>

                            <div className="space-y-2">
                                {vehicles.map((v) => (
                                    <div key={v.id} className="p-2 bg-slate-50 dark:bg-dark-700 rounded-lg flex items-center gap-3">
                                        <div className={`w-2 h-2 rounded-full ${getStatusColor(v.status)}`} />
                                        <div className="flex-1 min-w-0">
                                            <p className="text-sm font-medium text-slate-900 dark:text-slate-100 truncate">{v.plateNumber}</p>
                                            <p className="text-xs text-slate-500">{v.routeName}</p>
                                        </div>
                                        <div className="text-right">
                                            <p className="text-sm font-mono text-slate-700 dark:text-slate-300">{v.speed} km/h</p>
                                        </div>
                                    </div>
                                ))}
                            </div>
                        </motion.div>
                    )}

                    {/* 告警中心 */}
                    {activeSection === 'alerts' && (
                        <motion.div
                            key="alerts"
                            initial={{ opacity: 0 }}
                            animate={{ opacity: 1 }}
                            exit={{ opacity: 0 }}
                            className="p-4 space-y-2"
                        >
                            {alerts.length === 0 ? (
                                <div className="text-center py-8 text-slate-500">
                                    <CheckCircle className="w-12 h-12 mx-auto mb-2 opacity-50" />
                                    <p>{t('transit.noAlerts')}</p>
                                </div>
                            ) : (
                                alerts.map((alert) => (
                                    <motion.div
                                        key={alert.id}
                                        initial={{ x: 20, opacity: 0 }}
                                        animate={{ x: 0, opacity: 1 }}
                                        className={`p-3 rounded-lg border ${getAlertColor(alert.type)}`}
                                    >
                                        <div className="flex items-start gap-2">
                                            <AlertTriangle className="w-4 h-4 mt-0.5 flex-shrink-0" />
                                            <div className="flex-1 min-w-0">
                                                <p className="text-sm">{alert.message}</p>
                                                <p className="text-xs opacity-70 mt-1">
                                                    {alert.time.toLocaleTimeString()}
                                                </p>
                                            </div>
                                        </div>
                                        <div className="flex gap-2 mt-2">
                                            <button className="text-xs px-2 py-1 rounded bg-white/50 dark:bg-dark-600 hover:bg-white dark:hover:bg-dark-500 transition-colors">
                                                {t('transit.processAlert')}
                                            </button>
                                            <button className="text-xs px-2 py-1 rounded hover:bg-white/30 dark:hover:bg-dark-600 transition-colors">
                                                {t('transit.ignoreAlert')}
                                            </button>
                                        </div>
                                    </motion.div>
                                ))
                            )}
                        </motion.div>
                    )}

                    {/* KPI 指标 */}
                    {activeSection === 'kpi' && (
                        <motion.div
                            key="kpi"
                            initial={{ opacity: 0 }}
                            animate={{ opacity: 1 }}
                            exit={{ opacity: 0 }}
                            className="p-4 space-y-3"
                        >
                            {kpiMetrics.map((metric, i) => (
                                <div key={i} className="p-4 bg-slate-50 dark:bg-dark-700 rounded-lg">
                                    <div className="flex items-center gap-3">
                                        <div className={`p-2 rounded-lg bg-${metric.color}-500/10`}>
                                            <metric.icon className={`w-5 h-5 text-${metric.color}-500`} />
                                        </div>
                                        <div>
                                            <p className="text-xs text-slate-500">{metric.label}</p>
                                            <p className="text-xl font-bold text-slate-900 dark:text-slate-100">
                                                {metric.value}
                                                {metric.unit && <span className="text-sm font-normal ml-1">{metric.unit}</span>}
                                            </p>
                                        </div>
                                    </div>
                                </div>
                            ))}
                            <div className="p-4 bg-gradient-to-r from-green-500/10 to-emerald-500/10 rounded-lg border border-green-500/20">
                                <div className="flex items-center gap-2 text-green-600 dark:text-green-400">
                                    <CheckCircle className="w-5 h-5" />
                                    <span className="font-medium">{t('transit.allNormal')}</span>
                                </div>
                            </div>
                        </motion.div>
                    )}

                    {/* 电子站牌 */}
                    {activeSection === 'signage' && (
                        <motion.div
                            key="signage"
                            initial={{ opacity: 0 }}
                            animate={{ opacity: 1 }}
                            exit={{ opacity: 0 }}
                            className="p-4 space-y-3"
                        >
                            <p className="text-xs text-slate-500 mb-2">{t('transit.signagePreview')}</p>
                            {signage.map((info, i) => (
                                <div key={i} className="p-3 bg-slate-900 dark:bg-dark-900 rounded-lg text-white">
                                    <div className="flex items-center justify-between mb-2">
                                        <span className="px-2 py-0.5 bg-primary-500 rounded text-xs font-bold">{info.routeCode}</span>
                                        <span className="text-xs text-slate-400">{t('transit.destination')}: {info.destination}</span>
                                    </div>
                                    <div className="flex items-center justify-between">
                                        <div>
                                            <p className="text-xs text-slate-400">{t('transit.nextArrival')}</p>
                                            <p className="text-2xl font-bold text-green-400">{info.nextArrival} <span className="text-sm">{t('transit.minutes')}</span></p>
                                        </div>
                                        <div className="text-right">
                                            <p className="text-xs text-slate-400">{t('transit.vehicleNumber')}</p>
                                            <p className="text-sm font-mono">{info.vehicleNumber}</p>
                                        </div>
                                    </div>
                                </div>
                            ))}
                        </motion.div>
                    )}
                </AnimatePresence>
            </div>
        </div>
    )
}

export default MonitoringPanel
